import {createElement} from '../render.js';

const createPointTemplate = (props) =>
  (`<div class="event">
  <time class="event__date" datetime="${props.startDate}">${props.startDate.getMonth()} ${props.startDate.getDate()}</time>
  <div class="event__type">
    <img class="event__type-icon" width="42" height="42" src="img/icons/${props.type.toLowerCase()}.png" alt="Event type icon">
  </div>
  <h3 class="event__title">${props.type} ${props.destination.cityName}</h3>
  <div class="event__schedule">
    <p class="event__time">
      <time class="event__start-time" datetime="${props.startDate}">${props.startDate.getHours()}:${props.startDate.getMinutes()}</time>
      &mdash;
      <time class="event__end-time" datetime="${props.endDate}">${props.endDate.getHours()}:${props.endDate.getMinutes()}</time>
    </p>
    <p class="event__duration">30M</p>
  </div>
  <p class="event__price">
    &euro;&nbsp;<span class="event__price-value">${props.cost}</span>
  </p>
  <h4 class="visually-hidden">Offers:</h4>
  <ul class="event__selected-offers">
    <li class="event__offer">
      <span class="event__offer-title">${props.offers.description}</span>
      &plus;&euro;&nbsp;
      <span class="event__offer-price">${props.offers.cost}</span>
    </li>
  </ul>
  <button class="event__favorite-btn event__favorite-btn--active" type="button">
    <span class="visually-hidden">Add to favorite</span>
    <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
      <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
    </svg>
  </button>
  <button class="event__rollup-btn" type="button">
    <span class="visually-hidden">Open event</span>
  </button>
</div>`);


export default class PointView {
  event;
  data;

  constructor(data) {
    this.data = data;
  }

  getTemplate() {
    return createPointTemplate(this.data);
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate(this.data));
    }

    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
